.dashboardWrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
}

.dashboardWrapper .sideNav {
  width: 18%;
  height: 100vh;
  background-image: linear-gradient(
    to top,
    #3f51b1 0%,
    #5a55ae 13%,
    #7b5fac 25%,
    #8f6aae 38%,
    #a86aa4 50%,
    #cc6b8e 62%,
    #f18271 75%,
    #f83600 87%,
    #f83600 100%
  );
}

.dashboardWrapper .mainContainer {
  width: 82%;
  height: 100vh;
  padding: 0px;
}
.dashboardWrapper .sideNav .profileInfo {
  width: 100%;
  background-image: linear-gradient(to right, #f83600 0%, #f9d423 100%);
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 10px;
  gap: 14px;
}
.dashboardWrapper .sideNav .profileInfo img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.dashboardWrapper .sideNav .profileInfo h3 {
  font-size: 20px;
}
.logoutContainer {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 80px;
  padding-bottom: 30px;
}
.logoutBtn {
  width: 140px;
  height: 40px;
  padding: 10px;
  color: white;
  background-color: rgb(191, 28, 66);
  outline: none;
  border: none;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
}
.logoutBtn:hover {
  background-color: crimson;
  transition: 0.3s ease-in-out;
  scale: 0.9;
}

.menuBar {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 30px;
  align-items: center;
}

.menuBar .menuLinks {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-size: 18px;
  text-decoration: none;
  color: crimson;
  background-color: white;
  border-radius: 8px;
  text-align: center;
}
.menuBar .menuLinks:hover {
  color: black;
  background-color: whitesmoke;
  transition: 0.2s ease-in-out;
  scale: 0.98;
}

.menuBar .menuIcons {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-right: 10px;
}


@media only screen and (max-width: 920px){
  .sideNav{
    display: none;
  }
  .dashboardWrapper .mainContainer {
    width: 100%;
    height: 100vh;
    padding: 0px;
  }
}
