.newInvoiceHeading {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  width: 100%;
  font-size: 25px;
  font-weight: bold;
}
.newInvoiceContainer {
  padding: 20px;
}

.newInvoiceHeader {
  display: flex;
  flex-direction: row;
}
.newInvoiceForm {
  padding: 10px 100px;
}
.newInvoiceForm .formRow {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  gap: 20px;
  margin-bottom: 20px;
}
.newInvoiceForm input {
  padding: 8px 15px;
  width: 250px;
  border-radius: 8px;
  outline: none;
  border: none;
  background-color: #e3d0ff;
  color: #47119a;
  font-size: 15px;
  letter-spacing: 1px;
}
.newInvoiceForm input::placeholder {
  color: #b084f4;
}
.newInvoiceForm .formRow .addressRow {
  height: 80px;
  width: 400px;
  padding: 10px;
  border-radius: 5px;
  outline: none;
  border: none;
  background-color: #e3d0ff;
  color: #47119a;
  font-size: 15px;
  letter-spacing: 1px;
}

.newInvoiceForm .formRow .addressRow::placeholder {
  color: #b084f4;
}
.saveBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: white;
  background-color: green;
  padding: 8px 10px;
  height: max-content;
  border-radius: 5px;
  width: fit-content;
}
.saveBtn:hover {
  background-color: #075707;
  transition: 0.2s ease-in-out;
}
.newInvoiceForm .addBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #e3d0ff;
  background-color: #47119a;
  padding: 5px 10px;
  border-radius: 5px;
}
.newInvoiceForm .addBtn:hover {
  background-color: #310770;
  transition: 0.2s ease-in-out;
}
.productList {
  width: 100%;
  height: 200px;
  padding: 10px 50px;
  background-color: rgb(247, 242, 255);
  color: #310770;
  font-size: 15px;
  border-radius: 12px;
  overflow-y: scroll;
}
.productList .products {
  display: flex;
  flex-direction: row;
  margin: 10px 0;
}
.productList .products p {
  margin-top: 5px;
  text-align: left;
  width: 20%;
}
.productList .products h5 {
  margin-top: 5px;
  text-align: left;
  width: 20%;
}
.totalPrice {
  display: flex;
  justify-content: flex-end;
}
.totalPrice h5 {
  width: 20%;
  margin-top: 30px;
  font-size: 18px;
  color: blueviolet;
}


@media only screen and (max-width: 920px){
  .newInvoiceForm {
    padding: 10px 20px;
  }
  .newInvoiceForm input {
    padding: 8px 10px;
  }
}

@media only screen and (max-width: 1024px){
  .newInvoiceForm {
    padding: 10px 50px;
  }
}

@media only screen and (max-width: 768px){
  .newInvoiceForm input{
    width: 185px;
  }
}

@media only screen and (max-width: 520px){
  .newInvoiceForm .formRow {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  .newInvoice{
    height: 100%;
    width: 100%;
  }
  .newInvoiceForm input {
    padding: 5px 15px;
    width: 280px;
    border-radius: 5px;
    font-size: 13px;
  }
  .newInvoiceForm .addressRow {
    padding: 5px 15px;
    width: 280px !important ;
  }
  .newInvoiceContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
  }
  .newInvoiceHeader{
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
}
