* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.homeWrapper {
  height: 100%;
  width: 100%;
}
.homeContainer {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.homeContainer .header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  padding: 10px 30px;
  color: white;
  background-image: linear-gradient(to right, #da4167 0%, #f9d423 100%);
  width: 100%;
}

.header h1 {
  font-family: "Lobster Two", cursive;
}

.homeContainer .header .homeBtn {
  background-color: #da4167;
  color: white;
  outline: none;
  border: none;
  font-family: "Nunito", sans-serif;
}

.homeContainer .main {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
  width: 100%;
}
.homeContainer .main .homeRight {
  height: 100%;
  width: 100%;
}
.homeContainer .main .homeLeft {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 50px;
}

.homeLeft p {
  margin-top: 60px;
  margin-bottom: 20px;
  font-size: 13px;
  letter-spacing: 1px;
  color: #4fb0e4;
}
.homeRight img {
  width: 100%;
  height: auto;
  margin-top: 30px;
}
.header .link {
  color: white;
}
.homeContainer .main .homeLeft h2 {
  font-size: 50px;
  color: royalblue;
}
.homeContainer .main .homeLeft h3 {
  padding-top: 10px;
  color: royalblue;
}
.homeExtra {
  display: flex;
  flex-direction: column; /* Stack rows */
  gap: 80px; /* Space between rows */
  margin-bottom: 80px;
}

.homeAbout,
.homeFeatures {
  width: 65%; /* Adjust width as needed */
}

.homeWhy {
  display: flex;
  justify-content: space-between;
  margin-right: 30px;
}

.homeWhy img {
  width: 25%;
  border-radius: 12px;
}

.homeFea {
  display: flex;
  justify-content: space-between;
  margin-left: 30px;
}

.homeFea img {
  width: 25%;
  border-radius: 12px;
}

.homeAbout {
  align-self: flex-start; /* Align container to the left */
  margin-left: 30px;
}

.homeFeatures {
  align-self: flex-end; /* Align container to the right */
  margin-right: 30px;
}
.homeFeatures h2 {
  color: #da4167;
}

.homeAbout ul,
.homeFeatures ul {
  background: linear-gradient(135deg, #da4167, #ffad61);
  padding: 30px;
  border-radius: 10px;
}

.homeAbout ul li,
.homeFeatures ul li {
  list-style: circle;
  color: white;
  margin-bottom: 15px;
}

.homeAbout .why {
  font-size: 24px;
  color: #da4167;
}

.homeAbout p,
.homeFeatures p {
  font-size: 18px;
  margin-top: 10px;
  color: #333;
  margin-bottom: 15px;
}

.homeTestimonials {
  margin: 80px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.homeTestimonials h2 {
  color: #da4167;
  margin-bottom: 20px;
}

.homeTestimonials .homeReview {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  gap: 30px;
}
.homeTestimonials .homeReview .review {
  padding: 30px;
  color: white;
  width: 300px;
  height: 250px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: space-between;
  letter-spacing: 1px;
  border-radius: 12px;
}
.homeTestimonials .homeReview .review:nth-child(1){
    background-image: linear-gradient(to top, #ff5982 0%, #fa7850 100%); 
}
.homeTestimonials .homeReview .review:nth-child(2){
    background-image: linear-gradient(to top, #fed64f 0%, #e48d72 100%); 
}
.homeTestimonials .homeReview .review:nth-child(3){
    background-image: linear-gradient(to top, #1ccae0 0%, #4177ff 100%); 
}
.homeTestimonials .homeReview .review:hover {
  scale: 0.97;
  transition: 0.2s ease-in-out;
  cursor: pointer;
}
.homeTestimonials .homeReview .review p {
  font-size: 18px;
}
.homeTestimonials .homeReview .review span {
  font-size: 15px;
  text-align: end;
}

.homeCTA {
  margin: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.homeCTA h2 {
  color: #da4167;
  font-size: 24px;
}

.homeCTA p {
  color: #333;
  margin-bottom: 10px;
}

.homeCTA .homeBtn {
  background-color: #da4167;
  color: white;
}
.homeCTA .homeBtn:hover {
  background-color: #9c1134;
  color: white;
  scale: 1.1;
  transition: 0.2s ease-in-out;
}

.homeBtn:hover {
  background-color: rgb(0, 64, 255);
  color: white;
  scale: 1.1;
  transition: 0.2s ease-in-out;
}
