@media only screen and (max-width: 2000px){
.dashResponsive{
    display: none;
}
}
@media only screen and (max-width: 920px){
    .dashboardWrapper{
        flex-direction: column;
    }
    .dashResponsive{
        height: 80px;
        background-color: #DA4167;
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 5px 10px;
    }
    .dashResponsive .profileInfo img{
    border-radius: 50%;
    }
    .dashResponsive .profileInfo{
        display: flex;
        flex-direction: row;
        gap: 20px;
        align-items: center;
        color: white;
        font-size: 24px;
    }
    .dashResponsive .hamburgerMenu{
        display: flex;
        align-items: center;
        color: white;
    }

    .hamburgerMenu .icon{
        font-size: 40px;
        cursor: pointer;
    }

    .responsiveMenu {
        position: absolute;
        top: 80px;
        right: 10px;
        background-color: white;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        border-radius: 8px;
        padding: 10px;
        z-index: 1000;
        display: flex;
        flex-direction: column;
        gap: 20px;
        width: 250px;
      }
      
      .responsiveMenu .menuLinks {
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 10px;
        color: crimson;
        background-color: white;
        border-radius: 8px;
        text-decoration: none;
      }
      
      .responsiveMenu .menuLinks:hover {
        background-color: #f0f0f0;
        transition: 0.2s ease-in-out;
      }

      .responsiveMenu .logoutBtn{
        background-color: #DA4167;
        color: white;
      }
      .responsiveMenu .logoutBtn:hover{
        background-color: #ed6c8c;
      }

}



