.homeMainWrapper {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}
.homeMainWrapper .homeMainContainer1 {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 30px;
  justify-content: space-around;
  align-items: center;
}
.homeMainWrapper .homeMainContainer1 .homeMainBox1 {
  background-image: linear-gradient(
    to right,
    #f78ca0 0%,
    #f9748f 19%,
    #fd868c 60%,
    #fe9a8b 100%
  );
  width: 250px;
  height: 150px;
  text-align: center;
  color: white;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.homeMainWrapper .homeMainContainer1 .homeMainBox2 {
  background-image: linear-gradient(
    to right,
    #f78ca0 0%,
    #f9748f 19%,
    #fd868c 60%,
    #fe9a8b 100%
  );
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 150px;
  text-align: center;
  color: white;
  border-radius: 5px;
}
.homeMainWrapper .homeMainContainer1 .homeMainBox3 {
  background-image: linear-gradient(
    to right,
    #f78ca0 0%,
    #f9748f 19%,
    #fd868c 60%,
    #fe9a8b 100%
  );
  width: 250px;
  height: 150px;
  text-align: center;
  color: white;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.homeMainWrapper .homeMainContainer2 {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-top: 30px;
  justify-content: space-around;
  align-items: center;
  padding: 20px;
  gap: 20px;
}

.homeMainWrapper .homeMainContainer2 .chartBox {
  width: 60%;
  height: 50vh;
  background-color: #f7f2ff;
}

.homeMainWrapper .homeMainContainer2 .RecentInvoiceBox {
  width: 50%;
  height: 50vh;
  background-color: #f7f2ff;
  padding: 10px;
  display: flex;
  flex-direction: column;
}
.homeMainWrapper .homeMainContainer2 .RecentInvoiceBox h2 {
  font-size: 20px;
  text-align: center;
  color: white;
  background-color: #b889ff;
  padding: 8px 0;
  margin-bottom: 10px;
}

.homeMainWrapper .homeMainContainer2 .RecentInvoiceBox div {
  margin: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0px 10px;
}
.homeMainWrapper .homeMainContainer2 .RecentInvoiceBox div:hover {
  background-color: #ecdeff;
  transition: 0.2s;
  cursor: pointer;
}
.homeMainWrapper .homeMainContainer2 .RecentInvoiceBox div span {
  font-size: 15px;
  width: 50%;
  color: #954eff;
}
.homeMainWrapper .homeMainContainer2 .RecentInvoiceBox div button {
  padding: 5px 10px;
  cursor: pointer;
  background-color: green;
  color: white;
  border: none;
  outline: none;
  border-radius: 5px;
}
.homeMainWrapper .homeMainContainer2 .RecentInvoiceBox div button:hover {
  background-color: rgb(22, 195, 22);
  transition: 0.2s ease-in-out;
}


@media only screen and (max-width: 920px) {
  .homeMainWrapper .homeMainContainer1 .homeMainBox1, .homeMainBox2, .homeMainBox3{
    width: 160px;
    height: 100px;
  }
  .homeMainWrapper .homeMainContainer1 .homeMainBox2{
    width: 160px;
    height: 100px;
  }
  .homeMainWrapper .homeMainContainer1 .homeMainBox3{
    width: 160px;
    height: 100px;
  }

  .homeMainWrapper .homeMainContainer2 {
    display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 30px;
  justify-content: space-around;
  align-items: center;
  gap: 30px;
  }

  .homeMainWrapper .homeMainContainer2  .chartBox{
    width: 80%;
    height: max-content;
  }
  .homeMainWrapper .homeMainContainer2  .RecentInvoiceBox{
    height: max-content;
    width: 80%;
  }
}

@media only screen and (max-width: 520px){

  .homeMainWrapper .homeMainContainer2  .chartBox{
    width: 95%;
    height: max-content;
  }

  .homeMainWrapper .homeMainContainer2  .RecentInvoiceBox{
    height: max-content;
    width: 95%;
  }

  .homeMainWrapper .homeMainContainer1 .homeMainBox1, .homeMainBox2, .homeMainBox3{
    width: 120px;
    height: 100px;
  }
  .homeMainWrapper .homeMainContainer1 .homeMainBox2{
    width: 120px;
    height: 100px;
  }
  .homeMainWrapper .homeMainContainer1 .homeMainBox3{
    width: 120px;
    height: 100px;
  }
  .homeMainWrapper .homeMainContainer1 {
    gap: 10px;
    padding: 20px;
  }
  .homeMainWrapper .homeMainContainer1 h2{
    font-size: 16px;
  }

  .homeMainWrapper .homeMainContainer2 .RecentInvoiceBox h2 {
    font-size: 16px;
    padding: 5px 0;
    margin-bottom: 5px;
  }
  
  .homeMainWrapper .homeMainContainer2 .RecentInvoiceBox div {
    margin: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0px 0px;
  }
}
