.invoiceWrapper {
  padding: 10px 50px;
  width: 100%;
  height: 100%;
  background-color: rgb(236, 215, 255);
  overflow-y: scroll;
}

.invoiceWrapper h4 {
  font-size: 30px;
  color: blueviolet;
  text-align: center;
}
.invoiceBox {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  background-color: white;
  padding: 10px;
  margin-top: 15px;
  border-radius: 5px;
  border: 1px solid rgb(218, 175, 255);
  color: blueviolet;
}
.invoiceBox p {
  width: 30%;
}

.invoiceBox button {
  padding: 0px 5px;
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 5px;
}
.invoiceBox .deleteBtn {
  background-color: red;
  color: white;
}
.invoiceBox .deleteBtn:hover {
  background-color: rgb(255, 91, 123);
  color: white;
  transition: 0.2s ease-in-out;
}
.invoiceBox .viewBtn {
  background-color: green;
  color: white;
}
.invoiceBox .viewBtn:hover {
  background-color: rgb(2, 196, 2);
  color: white;
  transition: 0.2s ease-in-out;
}

@media only screen and (max-width: 520px){
  .invoiceWrapper {
    padding: 10px 20px;
  }
  .invoiceWrapper h4 {
    font-size: 25px;
    color: #DA4167;
  }

  .invoiceBox {
    gap: 13px;
    background-color: white;
    padding:6px 10px;
  }
}
