.registerWrapper{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    gap: 40px;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(-225deg, #231557 0%, #44107A 29%, #FF1361 67%, #FFF800 100%);
}
.registerWrapper h1{
    color: white;
}
.registerContainer{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: 70%;
    background-color: white;
    border-radius: 12px;
}
.registerLeft {
width: 120%;
height: 100%;
}

.registerRight{
width: 80%;
height: 100%;
padding: 50px 20px;
text-align: center;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}
.registerRight h2{
    color: #7d34eb;
}
.registerLeft {
background:url('../../assets/invoice.jpg');
background-size: cover;
border-radius: 12px 0 0 12px;
}

.registerRight .registerForm{
    display: flex;
    flex-direction: column;
    margin-top: none;
}
.logoBtn{
    color:#b68ff0;
    text-align: left;
    cursor: pointer;
}
.registerRight .registerForm input{
    margin-top: 20px;
    width: 300px;
    background-color: #e3d0ff;
    height: 40px;
    border: none;
    outline: none;
    border-radius: 20px;
    padding: 0 40px;
    letter-spacing: 1px;
}
.registerRight .registerForm input::placeholder{
    font-size: 13px;
    color: #b68ff0;
}
.registerRight .registerForm p{
    text-align: end;
    padding: 10px;
    color: #b67af6;
    text-decoration:underline;
    font-size: 14px;
    margin-bottom: 20px;
}

.registerRight .registerForm .inputContainer{
    position: relative;
}
.registerRight .registerForm .inputContainer .icon{
    position: absolute;
    top: 27px;
    left: 10px;
    color: #44107A;
}
.registerRight .registerForm .inputContainer .eyeIcon{
    position: absolute;
    top: 27px;
    right: 10px;
    color: #44107A;
    cursor: pointer;
} 

.registerRight .registerBtn{
    border-radius: 12px;
    margin-top: 20px;
}

.errorMessage {
    color: red;
    font-size: 14px;
    margin-top: 5px;
    text-align: center;
  }

.imagePreview{
    height: 40px;
    width: 50px;
    padding: 5px;
    border: none;
    outline: none;
    position: absolute;
    bottom: -0px;
    right: -50px;
}