@media only screen and (max-width: 760px) {
  .homeContainer .main .homeLeft h2 {
    font-size: 40px;
  }
  .homeContainer .main .homeLeft p {
    margin-top: 10px;
    font-size: 15px;
  }
}

@media only screen and (max-width: 520px) {
  .homeContainer .header {
    padding: 10px 10px;
  }
  .homeContainer {
    height: 92vh !important;
    margin-bottom: 30px;
  }
  .homeTestimonials .homeReview .review {
    padding: 20px;
    color: white;
    width: 300px !important;
    height: 250px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: space-between;
    letter-spacing: 1px;
    border-radius: 12px;
  }
  
}
@media only screen and (max-width: 920px){
  .homeTestimonials .homeReview .review {
    padding: 20px;
    color: white;
    width: 240px;
    height: 240px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: space-between;
    letter-spacing: 1px;
    border-radius: 12px;
  }
  .homeContainer {
    height: 70vh;
  }
  .homeFea img ,.homeWhy img{
    height: max-content;
  }
  .homeFea ,.homeWhy {
    height: max-content;
    align-items: center;
  }
}

@media only screen and (max-width: 490px) {
  .homeContainer .main .homeLeft{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
  .homeContainer .main .homeLeft h2 {
    font-size: 35px;
    text-align: center;
  }
  .homeContainer .main .homeLeft h3{
    text-align: center;
  }
  .homeContainer .main {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
  }
  .homeContainer .main .homeLeft p {
    margin-top: 10px;
    font-size: 13px;
    text-align: center;
  }
  .homeContainer .main .homeRight {
    width: 350px;
    display: flex;
    justify-content: center;
  }

  .homeContainer .main .homeLeft {
    padding: 30px;
  }
}


@media only screen and (max-width: 520px){
  .homeTestimonials .homeReview{
    flex-direction: column;
}
.homeAbout p{
  display: none;
}

.homeWhy {
  display: flex;
  flex-direction: column;
}
.homeAbout h2{
  text-align: center;
  margin-bottom: 15px;
}
.homeAbout,
.homeFeatures {
  width: 90%; /* Adjust width as needed */
}
.homeWhy img,
.homeFea img {
  display: none;
}

.homeFeatures h2{
  text-align: center;
  margin-bottom: 15px;
}
.homeFeatures p{
  display: none;
}

.homeCTA h2{
  text-align: center;
}

.homeCTA p{
  font-size: 15px;
  text-align: center;
}
}
