.loginWrapper{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    gap: 40px;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(-225deg, #231557 0%, #44107A 29%, #FF1361 67%, #FFF800 100%);
}
.loginWrapper h1{
    color: white;
}
.loginContainer{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: 70%;
    background-color: white;
    border-radius: 12px;
}
.loginLeft {
width: 120%;
height: 100%;
}

.loginRight{
width: 80%;
height: 100%;
padding: 50px 20px;
text-align: center;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}
.loginRight h2{
    color: #7d34eb;
}
.loginLeft {
background:url('../../assets/invoice.jpg');
background-size: cover;
border-radius: 12px 0 0 12px;
}

.loginRight .loginForm{
    display: flex;
    flex-direction: column;
    margin-top: 40px;
}
.loginRight .loginForm input{
    margin-top: 20px;
    width: 300px;
    background-color: #e3d0ff;
    height: 40px;
    border: none;
    outline: none;
    border-radius: 20px;
    padding: 0 40px;
    letter-spacing: 1px;
}
.loginRight .loginForm input::placeholder{
    font-size: 13px;
    color: #b68ff0;
}
.loginRight .loginForm p{
    text-align: end;
    padding: 10px;
    color: #b67af6;
    text-decoration:underline;
    font-size: 14px;
    margin-bottom: 20px;
}

.loginRight .loginForm .inputContainer{
    position: relative;
}
.loginRight .loginForm .inputContainer .icon{
    position: absolute;
    top: 27px;
    left: 10px;
    color: #44107A;
}
.loginRight .loginForm .inputContainer .eyeIcon{
    position: absolute;
    top: 27px;
    right: 10px;
    color: #44107A;
    cursor: pointer;
}

.loginRight .loginBtn{
    border-radius: 12px;
}

.errorMessage {
    color: red;
    font-size: 14px;
    margin-top: 5px;
    text-align: center;
  }