.invoiceViewWrapper {
  width: 100%;
  height: 100%;
  background-color: whitesmoke;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 30px;
}

.invoiceViewWrapper .invoiceViewContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 42%;
  min-height: 90%;
  background-color: white;
  margin: 10px;
  border: 1px solid black;
  padding: 10px;
  font-size: 10px;
}

.invoiceFooter {
  padding: 20px 30px;
  text-align: end;
}

.invoiceViewWrapper .invoiceViewContainer .companyName {
  text-align: center;
  font-size: 22px;
  font-weight: bold;
  border-bottom: 1px solid black;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: royalblue;
}
.invoiceViewWrapper .invoiceViewContainer .bondInfo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
  width: 100%;
  font-size: 10px;
  border-bottom: 1px solid black;
}
.invoiceSpan {
  display: flex;
  width: 100%;
  justify-content: center;
}
.invoiceDesc {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.invoiceViewWrapper .invoiceViewContainer .bondInfo .ownerDetail,
.customerDetail {
  display: flex;
  flex-direction: column;
}

.invoiceTable {
  margin-top: 15px;
  width: 100%;
  border-collapse: collapse;
}
.invoiceTable th,
td {
  border: 1px solid rgb(190, 215, 255);
  padding: 2px 5px;
}
.invoiceTable thead th {
  background-color: royalblue;
  color: white;
}
.invoiceTable tbody {
  text-align: left;
}
.invoiceTable tfoot td {
  background-color: royalblue;
  color: white;
}

.printInvoiceBtn {
  display: flex;
  flex-direction: row;
  gap: 10px;
  justify-content: center;
  align-items: center;
}

.printBtn {
  background-color: green;
  color: white;
  outline: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 3px 5px;
}
.printBtn:hover {
  background-color: rgb(2, 196, 2);
  color: white;
  transition: 0.2s ease-in-out;
}


@media only screen and (max-width: 920px){
  .invoiceViewWrapper .invoiceViewContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 65%;
    min-height: 64%;
    background-color: white;
    margin: 10px;
    border: 1px solid black;
    padding: 10px;
    font-size: 10px;
  }
}

@media only screen and (max-width: 520px){
   .invoiceViewWrapper{
    flex-direction: column;
   }
  .invoiceViewWrapper .invoiceViewContainer {
    width: 90%;
    min-height: 60%;
    font-size: 8px;
  }
  .invoiceViewWrapper .invoiceViewContainer .companyName {
    text-align: center;
    font-size: 22px;
  }
  .invoiceFooter {
    padding: 20px 10px;
    text-align: end;
  }
}
@media only screen and (max-width: 380px){
 .invoiceViewWrapper .invoiceViewContainer {
   width: 90%;
   min-height: 75%;
 }

}

@media only screen and (max-width: 350px){
  .invoiceViewWrapper .invoiceViewContainer {
    width: 90%;
    min-height: 52%;
  }
 }
