.settingsWrapper {
  width: 100%;
  height: 100%;
  padding: 30px 50px;
  text-align: center;
  background-color: whitesmoke;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}
.settingsHeading {
  width: 100%;
  cursor: pointer;
  font-size: 25px;
  color: royalblue;
  letter-spacing: 1px;
}
.settingsContainer {
  min-height: 300px;
  width: 60%;
  background-color: white;
  border-radius: 12px;
  padding: 40px 20px;
}

.settingsContainer .imageUpdate {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0px 30px;
  position: relative;
}

.settingsContainer img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  cursor: pointer;
}

.settingsContainer span {
  color: black;
  font-size: 20px;
}
.settingsContainer .updateBtn {
  height: max-content;
  padding: 10px;
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 5px;
  background-color: royalblue;
  color: white;
  text-align: center;
  display: flex;
  flex-direction: row;
  gap: 5px;
  align-items: center;
  font-size: 18px;
}
.settingsContainer .updateBtn:hover {
  background-color: green;
  transition: 0.2s ease-in-out;
}

.imageUpdate .edit {
  position: absolute;
  left: 90px;
  bottom: 1px;
  padding: 0px 4px;
  background-color: rgb(187, 172, 255);
  border-radius: 50%;
  color: white;
  cursor: pointer;
}

.companyNameUpdate {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0px 30px;
  position: relative;
  margin-top: 40px;
}
.companyNameUpdate input{
    padding: 12px;
    background-color: #e3d0ff;
    color: #9156eb;
    border: none;
    outline: none;
    border-radius: 5px;
    font-size: 15px;
}





@media only screen and (max-width: 520px){
  .settingsWrapper {
    padding: 30px 5px;
  }
  .settingsContainer {
    min-height: 300px;
    width: 95%;
    border-radius: 10px;
    padding: 20px;
  }
  .settingsContainer .imageUpdate {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0px 0px;
    position: relative;
  }
  .imageUpdate .edit {
    position: absolute;
    left: 60px;
    bottom: 1px;
    padding: 0px 4px;
    background-color: rgb(187, 172, 255);
    border-radius: 50%;
    color: white;
    cursor: pointer;
  }
  .companyNameUpdate {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0px 0px;
    position: relative;
    margin-top: 40px;
    gap: 10px;
  }
}

