.footer{
    background-color: black;
    color: white;
    padding: 2rem 1rem ;
}
.footerLogo{
    color: white;
    font-family: 'Lobster Two', cursive;
    font-size: 2rem;
    cursor: pointer;
    width: 100%;
}
.footerLogo:hover{
    color: #b64a07;
    transition: 0.2s;
}
.footerTop{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.footerIcons a{
    font-size: 2rem;
    margin-left: 1rem;
    text-decoration: none;
    color: white;
    cursor: pointer;
}
.footerIcons a:hover{
    color:#b64a07 ;
    transition: 0.2s;
}
.footer a{
    color: white;
    text-decoration: none;
}
.footer a:hover{
    color:#b64a07 ;
    transition: 0.2s;
}
.footer span{
    color:#b64a07 ; 
}

.footerMid{
    margin-top: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.footerBottom{
    display: flex;
    margin: 3rem 0 1rem 0;
    align-items: center;
    justify-content: center;
    text-align: center;
}

@media(max-width:550px){
    .footerIcons a{
        font-size: 1.5rem;
    }
    .footerMid{
        font-size: small;
    }
    .footerMid a{
        font-size: 0.6rem;
    }
    .footerMid h4{
        font-size: 0.7rem;
    }
    .footerBottom p{
        font-size: 0.8rem;
    }
    p{
        font-size: 0.6rem;
    }
}